import { ChangeDetectorRef, Component, forwardRef, Input, OnChanges, SimpleChanges } from '@angular/core';
import {
  ControlValueAccessor,
  FormBuilder,
  FormControl,
  FormGroup,
  NG_VALIDATORS,
  NG_VALUE_ACCESSOR,
  ReactiveFormsModule,
  ValidationErrors,
  Validator,
  Validators,
} from '@angular/forms';
import { AsyncPipe, CommonModule, NgFor } from '@angular/common';
import { MatInputModule } from '@angular/material/input';
import { MatAutocompleteModule } from '@angular/material/autocomplete';
import { TranslateModule } from '@ngx-translate/core';
import { UntilDestroy, untilDestroyed } from '@ngneat/until-destroy';
import {
  AddressFormFieldFacade,
  boxPattern,
  fieldConstant,
  GeographicalAddress,
  geographicalAddressFieldDependencies,
} from '@sales/address-form-field/data-access';
import { HouseNumberComponent } from './house-number.component';
import { SubHouseNumberComponent } from './sub-house-number.component';
import { BoxComponent } from './box.component';
import { ErrorEvent, EventFacade } from '@sales/events/data-access';
import { BehaviorSubject, Observable } from 'rxjs';
import { AddressUtil } from '../utils/address.util';
import { tap } from 'rxjs/operators';
import { conditionalPatternValidator } from '../validators/conditional.validator';
import { AddressUnit } from '@sales/shared/address/data-access';

@Component({
  standalone: true,
  imports: [
    CommonModule,
    MatInputModule,
    MatAutocompleteModule,
    ReactiveFormsModule,
    NgFor,
    AsyncPipe,
    TranslateModule,
    HouseNumberComponent,
    SubHouseNumberComponent,
    BoxComponent,
  ],
  selector: 'tg-sales-address-geographical-address',
  templateUrl: './geographical-address.component.html',
  providers: [
    {
      provide: NG_VALUE_ACCESSOR,
      useExisting: forwardRef(() => GeographicalAddressComponent),
      multi: true,
    },
    {
      provide: NG_VALIDATORS,
      useExisting: forwardRef(() => GeographicalAddressComponent),
      multi: true,
    },
  ],
})
@UntilDestroy()
export class GeographicalAddressComponent implements ControlValueAccessor, Validator, OnChanges {
  @Input() isSubmitted = false;
  @Input() streetAddressUnit: AddressUnit | undefined;

  resetFilteredOptionsTrigger = new BehaviorSubject<void>(undefined);

  houseNumberAddressUnitOptions$: Observable<AddressUnit[]> | undefined;
  houseNumberAddressUnitOptions: AddressUnit[] | undefined;

  selectedHousenumberValue: string | undefined = undefined;
  selectedSubHousenumberValue: string | undefined = undefined;

  geographicalAddressForm: FormGroup = new FormGroup({});

  onChange: ((value: GeographicalAddress) => void) | undefined;
  onTouch: (() => void) | undefined;

  constructor(
    private readonly cdr: ChangeDetectorRef,
    private readonly formBuilder: FormBuilder,
    private readonly eventFacade: EventFacade,
    private readonly addressFormFieldFacade: AddressFormFieldFacade
  ) {
    this.geographicalAddressForm = new FormGroup({
      [fieldConstant.HOUSE_NUMBER]: new FormControl(''),
      [fieldConstant.SUB_HOUSE_NUMBER]: new FormControl(''),
      [fieldConstant.BOX]: new FormControl(''),
    });
  }

  writeValue(value: GeographicalAddress | null): void {
    try {
      this.initForm(value);
      if (value?.housenumber) {
        this.updateAddressUnitOptions(value.housenumber, value.subhousenumber);
      }
    } catch (e: unknown) {
      this.eventFacade.triggerEvent(ErrorEvent(e));
    }
  }

  registerOnChange(fn: (value: GeographicalAddress) => void): void {
    this.onChange = fn;
  }

  registerOnTouched(fn: () => void): void {
    this.onTouch = fn;
  }

  validate(): ValidationErrors | null {
    if (this.geographicalAddressForm && this.geographicalAddressForm.valid) {
      return null;
    }
    this.isSubmitted = true;
    return { invalidForm: { valid: false } };
  }

  setDisabledState(isDisabled: boolean) {
    if (isDisabled) {
      this.geographicalAddressForm.get(fieldConstant.HOUSE_NUMBER)?.reset(null, { emitEvent: false });
      this.geographicalAddressForm.get(fieldConstant.HOUSE_NUMBER)?.disable({ emitEvent: false });

      this.geographicalAddressForm.get(fieldConstant.SUB_HOUSE_NUMBER)?.reset(null, { emitEvent: false });
      this.geographicalAddressForm.get(fieldConstant.SUB_HOUSE_NUMBER)?.disable({ emitEvent: false });

      this.geographicalAddressForm.get(fieldConstant.BOX)?.reset(null, { emitEvent: false });
      this.geographicalAddressForm.get(fieldConstant.BOX)?.disable({ emitEvent: false });
    } else {
      const housenumberControl = this.geographicalAddressForm.get(fieldConstant.HOUSE_NUMBER);
      if (housenumberControl && housenumberControl.valid) {
        this.geographicalAddressForm.get(fieldConstant.SUB_HOUSE_NUMBER)?.enable({ emitEvent: false });
        this.geographicalAddressForm.get(fieldConstant.BOX)?.enable({ emitEvent: false });
      }
      this.geographicalAddressForm.get(fieldConstant.HOUSE_NUMBER)?.enable({ emitEvent: false });
    }
  }

  ngOnChanges(changes: SimpleChanges): void {
    if (changes['streetAddressUnit']) {
      const currentValue: AddressUnit | undefined = changes['streetAddressUnit'].currentValue;
      const previousValue: AddressUnit | undefined = changes['streetAddressUnit'].previousValue;

      this.handleStreetAddressUnitChange(currentValue, previousValue);
    }
  }

  updateAddressUnitOptions(housenumber: string, subHousenumber: string | undefined = undefined) {
    if (housenumber && this.streetAddressUnit) {
      this.houseNumberAddressUnitOptions$ = AddressUtil.getGeographicalAddressAddressUnits(
        housenumber,
        this.streetAddressUnit.id,
        this.addressFormFieldFacade
      ).pipe(
        tap((addressUnits: AddressUnit[]) => {
          return (this.houseNumberAddressUnitOptions = addressUnits);
        })
      );

      this.selectedHousenumberValue = housenumber;
      if (subHousenumber) {
        this.selectedSubHousenumberValue = subHousenumber;
      }
      this.cdr.detectChanges();
    }
  }

  private triggerReset() {
    this.resetFilteredOptionsTrigger.next();
  }

  private handleStreetAddressUnitChange(
    currentValue: AddressUnit | undefined,
    previousValue: AddressUnit | undefined
  ): void {
    if (previousValue && !currentValue) {
      this.setDisabledState(true);
    } else if (currentValue !== previousValue) {
      this.triggerReset();
      this.resetFields(fieldConstant.STREET);
    }
  }

  private initForm(value: GeographicalAddress | null) {
    this.geographicalAddressForm = this.formBuilder.group({});
    const isHousenumberDisabled = !this.streetAddressUnit;

    this.geographicalAddressForm.addControl(
      fieldConstant.HOUSE_NUMBER,
      new FormControl(
        {
          value: value?.housenumber,
          disabled: isHousenumberDisabled,
        },
        [Validators.required, Validators.maxLength(6), Validators.pattern('\\d*')]
      )
    );

    const isSubHouseNumberAndBoxDisabled = !value?.housenumber;

    this.geographicalAddressForm.addControl(
      fieldConstant.SUB_HOUSE_NUMBER,
      new FormControl(
        {
          value: value?.subhousenumber,
          disabled: isSubHouseNumberAndBoxDisabled,
        },
        [Validators.pattern('[A-Za-z0-9]{0,6}')]
      )
    );
    this.geographicalAddressForm.addControl(
      fieldConstant.BOX,
      new FormControl(
        {
          value: value?.box,
          disabled: isSubHouseNumberAndBoxDisabled,
        },
        [conditionalPatternValidator(boxPattern)]
      )
    );

    this.geographicalAddressForm
      .get(fieldConstant.HOUSE_NUMBER)
      ?.valueChanges.pipe(untilDestroyed(this))
      .subscribe(() => {
        this.resetFields(fieldConstant.HOUSE_NUMBER);
      });

    // Whenever a value changes we want to find out if we have an address unit or a manual address
    this.geographicalAddressForm.valueChanges.pipe(untilDestroyed(this)).subscribe((value: GeographicalAddress) => {
      this.selectedHousenumberValue = value.housenumber;
      this.selectedSubHousenumberValue = value.subhousenumber;

      // Set the address unit and manual address
      const matchingAddressUnit = AddressUtil.findAddressBasedOnGeographicalAddress(
        this.houseNumberAddressUnitOptions,
        value
      );
      if (matchingAddressUnit) {
        value.isManualAddress = false;
        value.addressUnit = matchingAddressUnit;
      } else {
        value.isManualAddress = true;
      }

      if (this.onChange) {
        this.onChange(value);
      }
    });
  }

  private resetFields(changedField: string) {
    const fieldDependency = geographicalAddressFieldDependencies.find((dependency) => changedField === dependency.key);
    if (fieldDependency) {
      fieldDependency.enableAndReset.forEach((fieldName) => this.resetField(fieldName));
      fieldDependency.disableAndReset.forEach((fieldName) => this.resetField(fieldName, true));
    }
  }

  private resetField(fieldName: string, disabled: boolean = false) {
    return this.geographicalAddressForm.get(fieldName)?.reset(
      {
        value: null,
        disabled: disabled,
      },
      { emitEvent: false }
    );
  }
}
