import { Observable } from 'rxjs';
import { map } from 'rxjs/operators';
import { AddressFormFieldFacade, fieldConstant, GeographicalAddress } from '@sales/address-form-field/data-access';
import { AddressUnit, Municipality } from '@sales/shared/address/data-access';

export class AddressUtil {
  static getFilteredMunicipalities(filterValue: string, addressFormFieldFacade: AddressFormFieldFacade) {
    return addressFormFieldFacade.getMunicipalities().pipe(
      map((response: Municipality[]) => {
        return response.filter((municipality) => {
          const municipalityString = `${municipality.postalCode.toLowerCase()} - ${municipality.location.toLowerCase()}`;
          return municipalityString.indexOf(filterValue.toLowerCase()) === 0;
        });
      })
    );
  }

  static getFilteredStreets(value: string, postalCodeQuery: string, addressFormFieldFacade: AddressFormFieldFacade) {
    return addressFormFieldFacade.getStreets(value, postalCodeQuery).pipe();
  }

  static getGeographicalAddressAddressUnits(
    filterValue: string,
    streetAddressUnitId: string,
    addressFormFieldFacade: AddressFormFieldFacade
  ): Observable<AddressUnit[]> {
    return addressFormFieldFacade.getGeographicalAddresses(filterValue, streetAddressUnitId);
  }

  static filterHouseNumbers(houseNumberAddressUnitOptions: Observable<AddressUnit[]>) {
    return houseNumberAddressUnitOptions.pipe(
      map((addressUnits) => {
        return addressUnits
          .reduce((addressUnitList: AddressUnit[], addressUnit) => {
            const itemExists = addressUnitList.some(
              (existingAddressUnit) => existingAddressUnit.name === addressUnit.name
            );
            if (itemExists) {
              return addressUnitList;
            }
            return [...addressUnitList, addressUnit];
          }, [])
          .sort((a, b) => {
            try {
              return Number(a.name) - Number(b.name);
            } catch (e) {
              return 0;
            }
          });
      })
    );
  }

  static filterSubHouseNumbers(
    filterValue: string,
    houseNumberValue: string,
    houseNumberAddressUnitOptions: Observable<AddressUnit[]>
  ) {
    return houseNumberAddressUnitOptions.pipe(
      map((addressUnits) => {
        return addressUnits
          .filter((addressUnit) => {
            return (
              addressUnit.name === houseNumberValue &&
              addressUnit.subHouseNumber?.toLowerCase().startsWith(filterValue.toLowerCase())
            );
          })
          .reduce((addressUnitList: AddressUnit[], addressUnit) => {
            const itemExists = addressUnitList.some(
              (existingAddressUnit) => existingAddressUnit.subHouseNumber === addressUnit.subHouseNumber
            );
            if (itemExists) {
              return addressUnitList;
            }
            return [...addressUnitList, addressUnit];
          }, [])
          .sort((a, b) => {
            try {
              return Number(a.name) - Number(b.name);
            } catch (e) {
              return 0;
            }
          });
      })
    );
  }

  static filterBoxNumbers(
    filterValue: string,
    houseNumberValue: string,
    subHouseNumberValue: string | undefined,
    houseNumberAddressUnitOptions: Observable<AddressUnit[]>
  ) {
    return houseNumberAddressUnitOptions.pipe(
      map((addressUnits) => {
        return addressUnits
          .filter((addressUnit) => {
            const subHouseNumberMatches = subHouseNumberValue
              ? addressUnit.subHouseNumber === subHouseNumberValue
              : !addressUnit.subHouseNumber;

            return (
              addressUnit.name === houseNumberValue &&
              subHouseNumberMatches &&
              addressUnit.boxNumber?.toLowerCase().startsWith(filterValue.toLowerCase())
            );
          })
          .sort((a, b) => {
            try {
              return Number(a.name) - Number(b.name);
            } catch (e) {
              return 0;
            }
          });
      })
    );
  }

  static getFilteredSubHouseNumberOptions$(subHouseNumbers$: Observable<AddressUnit[]>) {
    return subHouseNumbers$.pipe(
      map((addressUnits) => {
        return addressUnits
          .filter((addressUnit) => addressUnit.subHouseNumber)
          .filter((addressUnit) => addressUnit.boxNumber === undefined);
      })
    );
  }

  static getMunicipalityValue(postalCode: string, addressUnit?: AddressUnit): Municipality | null {
    const municipality = this.getAddressValueFromAddressChain('Municipality', addressUnit);
    if (municipality?.name) {
      return {
        location: municipality.name,
        postalCode: postalCode,
      };
    }
    throw new Error('AddressFormField could not retrieve municipality');
  }

  static getAddressValue(value: string, addressUnit?: AddressUnit): AddressUnit | null {
    if (value === fieldConstant.COUNTRY || value === fieldConstant.MUNICIPALITY || value === fieldConstant.STREET) {
      return this.getAddressValueFromAddressChain(value, addressUnit);
    } else {
      return this.getTopLevelAddressValue(value, addressUnit);
    }
  }

  static findAddressBasedOnGeographicalAddress(
    houseNumberAddressUnitOptions: AddressUnit[] | undefined,
    value: GeographicalAddress
  ): AddressUnit | undefined {
    if (!houseNumberAddressUnitOptions) {
      return undefined;
    }
    return houseNumberAddressUnitOptions.find(
      (unit: AddressUnit) =>
        unit.name === value.housenumber &&
        unit.subHouseNumber === value.subhousenumber &&
        unit.boxNumber === value.box?.boxNumber &&
        unit.floor === value.box?.floor
    );
  }

  private static getAddressValueFromAddressChain(value: string, addressUnit?: AddressUnit): AddressUnit | null {
    const foundValue = addressUnit?.addressUnitChain?.find(
      (addressUnit) => addressUnit.type.toLowerCase() === value.toLowerCase()
    );
    return foundValue ? foundValue : null;
  }

  private static getTopLevelAddressValue(value: string, addressUnit?: AddressUnit): AddressUnit | null {
    switch (value) {
      case 'housenumber':
        return addressUnit ? addressUnit : null;
      case 'subhousenumber':
        if (addressUnit?.subHouseNumber) {
          return addressUnit;
        }
        break;
      case 'box':
        if (addressUnit?.boxNumber) {
          return addressUnit;
        }
        break;
    }
    return null;
  }
}
