import { UntilDestroy, untilDestroyed } from '@ngneat/until-destroy';
import {
  ControlValueAccessor,
  NG_VALIDATORS,
  NG_VALUE_ACCESSOR,
  ReactiveFormsModule,
  Validator,
  Validators,
} from '@angular/forms';
import { AfterViewInit, Component, forwardRef, Input, OnInit, ViewChild } from '@angular/core';
import { BaseComponent } from './base.component';
import { AsyncPipe, CommonModule, NgFor } from '@angular/common';
import { MatInputModule } from '@angular/material/input';
import { MatAutocompleteModule, MatAutocompleteTrigger } from '@angular/material/autocomplete';
import { TranslateModule } from '@ngx-translate/core';
import { EMPTY, iif, Observable, of } from 'rxjs';
import { AddressUtil } from '../utils/address.util';
import { debounceTime, distinctUntilChanged, startWith, switchMap } from 'rxjs/operators';
import { MatOptionSelectionChange } from '@angular/material/core';
import { AddressFormFieldFacade } from '@sales/address-form-field/data-access';
import { Municipality } from '@sales/shared/address/data-access';

@Component({
  standalone: true,
  imports: [
    CommonModule,
    MatInputModule,
    MatAutocompleteModule,
    ReactiveFormsModule,
    NgFor,
    AsyncPipe,
    TranslateModule,
  ],
  selector: 'tg-sales-address-municipality',
  templateUrl: './municipality.component.html',
  providers: [
    {
      provide: NG_VALUE_ACCESSOR,
      useExisting: forwardRef(() => MunicipalityComponent),
      multi: true,
    },
    {
      provide: NG_VALIDATORS,
      useExisting: forwardRef(() => MunicipalityComponent),
      multi: true,
    },
  ],
})
@UntilDestroy()
export class MunicipalityComponent
  extends BaseComponent<Municipality>
  implements ControlValueAccessor, Validator, OnInit, AfterViewInit
{
  @Input() isSubmitted = false;

  @ViewChild('input', { read: MatAutocompleteTrigger }) autoCompleteTrigger: MatAutocompleteTrigger | undefined;

  options: string[] = [];
  filteredOptions!: Observable<Municipality[]>;

  onChange: ((value: Municipality) => void) | undefined;
  onTouch: (() => void) | undefined;

  constructor(private readonly addressFormFieldFacade: AddressFormFieldFacade) {
    super();
    this.control.addValidators(Validators.required);
  }

  ngOnInit(): void {
    this.setupFilter();
  }

  setupFilter(): void {
    this.filteredOptions = this.control.valueChanges.pipe(
      startWith(''),
      debounceTime(200),
      distinctUntilChanged(),
      switchMap((val) => iif(() => val && val.length > 1, this._filterOptions(val || ''), EMPTY)),
      untilDestroyed(this)
    );
  }

  registerOnChange(fn: (value: Municipality) => void): void {
    this.onChange = fn;
  }

  registerOnTouched(fn: () => void): void {
    this.onTouch = fn;
  }

  writeValue(location: Municipality | null): void {
    this.control.setValue(location);
  }

  display(municipality: Municipality | undefined): string {
    return municipality ? `${municipality.postalCode} - ${municipality.location}` : '';
  }

  ngAfterViewInit(): void {
    this.autoCompleteTrigger?.panelClosingActions
      .pipe(untilDestroyed(this))
      .subscribe((event: MatOptionSelectionChange | null) => {
        this.handleAutoCompleteSelection(event);
      });
  }

  _filterOptions(value: string | Municipality): Observable<Municipality[]> {
    if (typeof value === 'string') {
      const filterValue = value.toLowerCase();
      return AddressUtil.getFilteredMunicipalities(filterValue, this.addressFormFieldFacade);
    }
    return of([]);
  }

  setDisabledState(isDisabled: boolean) {
    this.disabledState = isDisabled;
  }

  private handleAutoCompleteSelection(event: MatOptionSelectionChange | null): void {
    if (event && event.isUserInput) {
      this.handleUserInputSelection(event.source.value);
    } else if (!event && this.autoCompleteTrigger && this.autoCompleteTrigger.activeOption) {
      this.handleActiveOptionSelection(this.autoCompleteTrigger.activeOption.value);
    }
  }

  private handleUserInputSelection(value: Municipality): void {
    if (value && this.onChange) {
      this.control.setValue(value);
      this.onChange(value);
    }
  }

  private handleActiveOptionSelection(value: Municipality): void {
    this.handleUserInputSelection(value);
  }
}
