import { Observable } from 'rxjs';
import { SimpleChange } from '@angular/core';
import { AbstractControl, FormControl, ValidationErrors } from '@angular/forms';

export abstract class BaseComponent<T> {
  control = new FormControl();
  disabledState = true;

  abstract _filterOptions(value: string): Observable<T[]>;

  validate(control: AbstractControl): ValidationErrors | null {
    return control.valid ? null : { required: true };
  }

  _setDisabledStateOnChange(change: SimpleChange): void {
    if (change) {
      this._setDisabledState(change.currentValue === null);
    }
  }

  _setDisabledState(disabledState: boolean): void {
    this.disabledState = disabledState;
    if (disabledState) {
      this.control.disable();
    } else {
      this.control.enable();
    }
  }
}
