import { Component, forwardRef, Input } from '@angular/core';
import {
  ControlValueAccessor,
  FormBuilder,
  FormControl,
  FormControlState,
  FormGroup,
  NG_VALIDATORS,
  NG_VALUE_ACCESSOR,
  ReactiveFormsModule,
  ValidationErrors,
  Validator,
  ValidatorFn,
  Validators,
} from '@angular/forms';
import { AsyncPipe, CommonModule, NgFor } from '@angular/common';
import { MatInputModule } from '@angular/material/input';
import { MatAutocompleteModule } from '@angular/material/autocomplete';
import { TranslateModule } from '@ngx-translate/core';
import { UntilDestroy, untilDestroyed } from '@ngneat/until-destroy';
import { MunicipalityComponent } from './components/municipality.component';
import { StreetComponent } from './components/street.component';
import { HouseNumberComponent } from './components/house-number.component';
import { SubHouseNumberComponent } from './components/sub-house-number.component';
import { BoxComponent } from './components/box.component';
import {
  AddressFieldValue,
  fieldConstant,
  fieldDependencies,
  fieldDependency,
  GeographicalAddress,
  StreetValue,
} from '@sales/address-form-field/data-access';
import { ErrorEvent, EventFacade } from '@sales/events/data-access';
import { AddressUtil } from './utils/address.util';

import { GeographicalAddressComponent } from './components/geographical-address.component';
import { InitialAddress } from '@sales/delivery/data-access';
import { AddressTypeIdConstants, AddressUnit, Municipality } from '@sales/shared/address/data-access';

@Component({
  standalone: true,
  imports: [
    CommonModule,
    MatInputModule,
    MatAutocompleteModule,
    ReactiveFormsModule,
    NgFor,
    AsyncPipe,
    TranslateModule,
    MunicipalityComponent,
    StreetComponent,
    HouseNumberComponent,
    SubHouseNumberComponent,
    BoxComponent,
    GeographicalAddressComponent,
  ],
  selector: 'tg-sales-address-form-field',
  templateUrl: './address-form-field.component.html',
  providers: [
    {
      provide: NG_VALUE_ACCESSOR,
      useExisting: forwardRef(() => AddressFormFieldComponent),
      multi: true,
    },
    {
      provide: NG_VALIDATORS,
      useExisting: forwardRef(() => AddressFormFieldComponent),
      multi: true,
    },
  ],
})
@UntilDestroy()
export class AddressFormFieldComponent implements ControlValueAccessor, Validator {
  @Input() isSubmitted = false;
  @Input() manualAddressIsAllowed = true;

  initialAddress: InitialAddress | undefined;
  addressForm: FormGroup = new FormGroup({});
  configuredAddress: AddressFieldValue = {};

  onChange: ((value: AddressFieldValue) => void) | undefined;
  onTouch: (() => void) | undefined;

  protected readonly FormControl = FormControl;

  constructor(
    private readonly formBuilder: FormBuilder,
    private readonly eventFacade: EventFacade
  ) {
    this.addressForm = new FormGroup({
      [fieldConstant.MUNICIPALITY]: new FormControl(''),
      [fieldConstant.STREET]: new FormControl(''),
      [fieldConstant.GEOGRAPHICAL_ADDRESS]: new FormControl(''),
    });
  }

  writeValue(address: InitialAddress): void {
    try {
      this.initialAddress = address;
      this.initForm();

      const field = this.findLastNonEmptyField();
      if (field) {
        this.resetFields(field);
      }
    } catch (e: unknown) {
      this.eventFacade.triggerEvent(ErrorEvent(e));
    }
  }

  registerOnChange(fn: (value: AddressFieldValue) => void): void {
    this.onChange = fn;
  }

  registerOnTouched(fn: () => void): void {
    this.onTouch = fn;
  }

  validate(): ValidationErrors | null {
    if (this.addressForm && this.addressForm.valid) {
      return null;
    }
    this.isSubmitted = true;
    return { invalidForm: { valid: false } };
  }

  /**
   * Finds the first field that has changed between the current and previous address field values.
   * @param previousValues The previous address field values.
   * @param currentValues The current address field values.
   * @returns The key of the first changed field, or null if no fields have changed.
   */
  findChangedField(previousValues: AddressFieldValue, currentValues: AddressFieldValue): string | null {
    for (const key in currentValues) {
      if (currentValues[key]?.postalCode && currentValues[key]?.postalCode !== previousValues[key]?.postalCode) {
        return key;
      }
      if (currentValues[key]?.value !== previousValues[key]?.value) {
        return key;
      }
    }
    return null;
  }

  private initForm() {
    this.addressForm = this.formBuilder.group({});
    this.createMunicipalityFormControl([Validators.required]);
    this.createStreetFormControl();
    this.createGeographicalAddressFormControl();

    this.addressForm.valueChanges.pipe(untilDestroyed(this)).subscribe((value: AddressFieldValue) => {
      const changedField = this.findChangedField(this.configuredAddress, value);
      this.configuredAddress = value;
      if (changedField) {
        this.resetFields(changedField);
      }
      if (this.onChange && this.onTouch) {
        this.onChange(value);
        this.onTouch();
      }
    });
  }

  private createMunicipalityFormControl(validators: ValidatorFn[]) {
    if (!this.initialAddress || !this.initialAddress?.postalCode) {
      throw new Error('No postal code exists');
    }
    const municipalityValue = AddressUtil.getMunicipalityValue(
      this.initialAddress.postalCode,
      this.initialAddress.addressUnit
    );
    const formControlState: FormControlState<Municipality | null> = {
      value: municipalityValue,
      disabled: !municipalityValue,
    };
    this.addressForm.addControl(fieldConstant.MUNICIPALITY, new FormControl(formControlState, validators));
    this.configuredAddress[fieldConstant.MUNICIPALITY] = municipalityValue;
  }

  private createStreetFormControl() {
    let streetAddressUnit: AddressUnit | null | undefined = this.initialAddress?.addressUnit;
    if (!(this.initialAddress?.addressUnit.addressUnitTypeId === AddressTypeIdConstants.STREETS)) {
      streetAddressUnit = AddressUtil.getAddressValue(fieldConstant.STREET, this.initialAddress?.addressUnit);
    }

    if (streetAddressUnit && this.initialAddress?.addressUnit) {
      const addressUnitValue: AddressUnit = this.setStreetAddressUnits(
        { ...streetAddressUnit },
        { ...this.initialAddress?.addressUnit }
      );

      const streetValue = {
        value: this.initialAddress.street,
        addressUnit: addressUnitValue,
      };

      if (addressUnitValue) {
        const formControlState: FormControlState<StreetValue | null> = {
          value: streetValue,
          disabled: false,
        };

        this.addressForm.addControl(fieldConstant.STREET, new FormControl(formControlState, [Validators.required]));
        this.configuredAddress[fieldConstant.STREET] = streetValue;
      }
    }
  }

  private createGeographicalAddressFormControl() {
    const geographicalAddressValue: GeographicalAddress = {
      housenumber: this.initialAddress?.houseNumber,
      subhousenumber: this.initialAddress?.subHouseNumber,
      box: {
        boxNumber: this.initialAddress?.box,
        floor: this.initialAddress?.floor,
      },
      isManualAddress: true,
    };

    if (this.initialAddress?.addressUnit.addressUnitTypeId === AddressTypeIdConstants.GEOGRAPHICAL_ADDRESS) {
      geographicalAddressValue.isManualAddress = false;
      geographicalAddressValue.addressUnit = this.initialAddress?.addressUnit;
    }
    const formControlState: FormControlState<GeographicalAddress | null> = {
      value: geographicalAddressValue,
      disabled: !geographicalAddressValue,
    };
    this.addressForm.addControl(
      fieldConstant.GEOGRAPHICAL_ADDRESS,
      new FormControl(formControlState, [Validators.required])
    );
    this.configuredAddress[fieldConstant.GEOGRAPHICAL_ADDRESS] = geographicalAddressValue;
  }

  private setStreetAddressUnits(streetAddressUnit: AddressUnit, addressUnitValue: AddressUnit) {
    const countryAddressUnit = AddressUtil.getAddressValue(fieldConstant.COUNTRY, addressUnitValue);
    const municipalityAddressUnit = AddressUtil.getAddressValue(fieldConstant.MUNICIPALITY, addressUnitValue);

    streetAddressUnit.addressUnitChain = [];

    if (countryAddressUnit && municipalityAddressUnit) {
      streetAddressUnit.addressUnitChain.push(countryAddressUnit, municipalityAddressUnit);
    }
    return streetAddressUnit;
  }

  private resetFields(changedField: string) {
    const fieldDependency = fieldDependencies.find((dependency) => changedField === dependency.key);
    if (fieldDependency) {
      fieldDependency.clear.forEach((fieldName) => this.clearField(fieldName));
      fieldDependency.enableAndReset.forEach((fieldName) => this.resetField(fieldName));
      fieldDependency.disableAndReset.forEach((fieldName) => this.resetField(fieldName, true));
    }
  }

  private clearField(fieldName: string) {
    if (this.configuredAddress) {
      delete this.configuredAddress[fieldName];
    }
  }

  private resetField(fieldName: string, disabled: boolean = false) {
    return this.addressForm.get(fieldName)?.reset(
      {
        value: null,
        disabled: disabled,
      },
      { emitEvent: false }
    );
  }

  /**
   * Finds the last non-empty field in the address form based on field dependencies.
   * Fields are considered non-empty if their value is not null.
   * @returns The key of the last non-empty field, or undefined if all fields are empty.
   */
  private findLastNonEmptyField(): string | undefined {
    const orderedDependencies: fieldDependency[] = fieldDependencies.sort(
      (a: fieldDependency, b: fieldDependency) => a.weight - b.weight
    );
    let previousFieldName = undefined;
    for (const dependency of orderedDependencies) {
      const control = this.addressForm.controls[dependency.key];

      if (control.value === null) {
        return previousFieldName;
      }
      previousFieldName = dependency.key;
    }

    return undefined;
  }
}
