import { UntilDestroy, untilDestroyed } from '@ngneat/until-destroy';
import {
  ControlValueAccessor,
  NG_VALIDATORS,
  NG_VALUE_ACCESSOR,
  ReactiveFormsModule,
  Validator,
  Validators,
} from '@angular/forms';
import { AfterViewInit, Component, forwardRef, Input, OnInit, ViewChild } from '@angular/core';
import { BaseComponent } from './base.component';
import { AsyncPipe, CommonModule, NgFor } from '@angular/common';
import { MatInputModule } from '@angular/material/input';
import { MatAutocompleteModule, MatAutocompleteTrigger } from '@angular/material/autocomplete';
import { TranslateModule } from '@ngx-translate/core';
import { EMPTY, iif, Observable, of, Subscription } from 'rxjs';
import { debounceTime, distinctUntilChanged, map, startWith, switchMap } from 'rxjs/operators';
import { MatOptionSelectionChange } from '@angular/material/core';
import { AddressUtil } from '../utils/address.util';
import { AddressUnit } from '@sales/shared/address/data-access';

@Component({
  standalone: true,
  imports: [
    CommonModule,
    MatInputModule,
    MatAutocompleteModule,
    ReactiveFormsModule,
    NgFor,
    AsyncPipe,
    TranslateModule,
  ],
  selector: 'tg-sales-address-sub-house-number',
  templateUrl: './sub-house-number.component.html',
  providers: [
    {
      provide: NG_VALUE_ACCESSOR,
      useExisting: forwardRef(() => SubHouseNumberComponent),
      multi: true,
    },
    {
      provide: NG_VALIDATORS,
      useExisting: forwardRef(() => SubHouseNumberComponent),
      multi: true,
    },
  ],
})
@UntilDestroy()
export class SubHouseNumberComponent
  extends BaseComponent<AddressUnit>
  implements ControlValueAccessor, Validator, OnInit, AfterViewInit
{
  @Input() resetTrigger!: Observable<void>;
  @Input() isSubmitted = false;
  @Input() selectedHouseNumberValue: string | undefined = undefined;
  @Input() houseNumberAddressUnitOptions: Observable<AddressUnit[]> | undefined;
  resetSubscription!: Subscription;

  @ViewChild('input', { read: MatAutocompleteTrigger }) autoCompleteTrigger: MatAutocompleteTrigger | undefined;

  filteredOptions!: Observable<string[]>;

  onChange: ((value: string | undefined) => void) | undefined;
  onTouch: (() => void) | undefined;

  constructor() {
    super();
    this.control.addValidators([Validators.pattern('[A-Za-z0-9]{0,6}')]);
  }

  ngOnInit(): void {
    this.setupFilter();
    this.listenForReset();
    this.control.statusChanges.pipe(untilDestroyed(this)).subscribe((status) => {
      this.isSubmitted = status === 'INVALID';
    });
  }

  setupFilter(): void {
    this.filteredOptions = this.control.valueChanges.pipe(
      startWith(''),
      debounceTime(200),
      distinctUntilChanged(),
      switchMap((val) => iif(() => val, this._filterOptions(val || ''), EMPTY)),
      map((addressUnits: AddressUnit[]) => addressUnits.map((addressUnit: AddressUnit) => addressUnit.subHouseNumber)),
      map((subHouseNumbers: (string | undefined)[]) =>
        subHouseNumbers.filter((subHouseNumber): subHouseNumber is string => subHouseNumber !== undefined)
      ),
      untilDestroyed(this)
    );
  }

  registerOnChange(fn: (value: string | undefined) => void): void {
    this.onChange = fn;
  }

  registerOnTouched(fn: () => void): void {
    this.onTouch = fn;
  }

  writeValue(value: string | null): void {
    this.control.setValue(value, { emitEvent: false });
  }

  ngAfterViewInit(): void {
    this.autoCompleteTrigger?.panelClosingActions
      .pipe(untilDestroyed(this))
      .subscribe((event: MatOptionSelectionChange | null) => {
        this.handleAutoCompleteSelection(event);
      });
  }

  _filterOptions(value: string): Observable<AddressUnit[]> {
    if (this.selectedHouseNumberValue && this.control.value && this.houseNumberAddressUnitOptions) {
      return AddressUtil.filterSubHouseNumbers(
        value,
        this.selectedHouseNumberValue,
        this.houseNumberAddressUnitOptions
      );
    }
    return of([]);
  }

  setDisabledState(isDisabled: boolean) {
    this._setDisabledState(isDisabled);
  }

  /**
   * Subscribes to the resetTrigger observable to listen for reset events, which are
   * emitted when the street value changes in the parent component. Upon receiving a
   * reset signal, this method clears the list of house numbers by setting `filteredOptions`
   * to an observable that emits an empty array. This ensures that the list of house numbers
   * is reset and no longer displays previous results that were relevant to the old street value.
   */
  private listenForReset() {
    this.resetSubscription = this.resetTrigger.pipe(untilDestroyed(this)).subscribe(() => {
      this.filteredOptions = of([]);
      this.setupFilter();
    });
  }

  private handleAutoCompleteSelection(event: MatOptionSelectionChange | null): void {
    if (event && event.isUserInput) {
      this.handleUserInputSelection(event.source.value);
    } else if (!event && this.autoCompleteTrigger && this.autoCompleteTrigger.activeOption) {
      this.handleActiveOptionSelection(this.autoCompleteTrigger.activeOption.value);
    } else {
      this.handleNoSelection();
    }
  }

  private handleUserInputSelection(value: string): void {
    if (value && this.onChange) {
      this.control.setValue(value);
      this.onChange(value);
    }
  }

  private handleActiveOptionSelection(value: string): void {
    this.handleUserInputSelection(value);
  }

  private handleNoSelection(): void {
    if (this.onChange) {
      if (this.control.value !== '') {
        this.onChange(this.control.value);
      } else {
        this.onChange(undefined);
      }
    }
  }
}
