import { UntilDestroy, untilDestroyed } from '@ngneat/until-destroy';
import {
  ControlValueAccessor,
  NG_VALIDATORS,
  NG_VALUE_ACCESSOR,
  ReactiveFormsModule,
  Validator,
  Validators,
} from '@angular/forms';
import {
  AfterViewInit,
  Component,
  forwardRef,
  Input,
  OnChanges,
  OnInit,
  SimpleChanges,
  ViewChild,
} from '@angular/core';
import { BaseComponent } from './base.component';
import { AsyncPipe, CommonModule, NgFor } from '@angular/common';
import { MatInputModule } from '@angular/material/input';
import { MatAutocompleteModule, MatAutocompleteTrigger } from '@angular/material/autocomplete';
import { TranslateModule } from '@ngx-translate/core';
import { EMPTY, iif, Observable, of } from 'rxjs';
import { AddressUtil } from '../utils/address.util';
import { debounceTime, distinctUntilChanged, startWith, switchMap } from 'rxjs/operators';
import { AddressFormFieldFacade, FieldValue, StreetValue } from '@sales/address-form-field/data-access';
import { MatOptionSelectionChange } from '@angular/material/core';
import { AddressUnit } from '@sales/shared/address/data-access';

@Component({
  standalone: true,
  imports: [
    CommonModule,
    MatInputModule,
    MatAutocompleteModule,
    ReactiveFormsModule,
    NgFor,
    AsyncPipe,
    TranslateModule,
  ],
  selector: 'tg-sales-address-street',
  templateUrl: './street.component.html',
  providers: [
    {
      provide: NG_VALUE_ACCESSOR,
      useExisting: forwardRef(() => StreetComponent),
      multi: true,
    },
    {
      provide: NG_VALIDATORS,
      useExisting: forwardRef(() => StreetComponent),
      multi: true,
    },
  ],
})
@UntilDestroy()
export class StreetComponent
  extends BaseComponent<AddressUnit>
  implements ControlValueAccessor, Validator, OnInit, AfterViewInit, OnChanges
{
  @Input() postalCode: string | undefined = undefined;
  @Input() isSubmitted = false;

  @ViewChild('input', { read: MatAutocompleteTrigger }) autoCompleteTrigger: MatAutocompleteTrigger | undefined;

  options: string[] = [];
  filteredOptions!: Observable<AddressUnit[]>;

  onChange: ((value: FieldValue | undefined) => void) | undefined;
  onTouch: (() => void) | undefined;

  constructor(private readonly addressFormFieldFacade: AddressFormFieldFacade) {
    super();
    this.control.addValidators(Validators.required);
  }

  ngOnInit(): void {
    this.setupFilter();
  }

  setupFilter(): void {
    this.filteredOptions = this.control.valueChanges.pipe(
      startWith(''),
      debounceTime(200),
      distinctUntilChanged(),
      switchMap((val) => iif(() => val && val.length > 2, this._filterOptions(val || ''), EMPTY)),
      untilDestroyed(this)
    );
  }

  registerOnChange(fn: (value: FieldValue | undefined) => void): void {
    this.onChange = fn;
  }

  registerOnTouched(fn: () => void): void {
    this.onTouch = fn;
  }

  writeValue(value: StreetValue | null): void {
    this.control.setValue(value?.addressUnit);
  }

  display(addressUnit: AddressUnit | undefined): string {
    return addressUnit && addressUnit.nameDU ? addressUnit.nameDU : '';
  }

  ngAfterViewInit(): void {
    this.autoCompleteTrigger?.panelClosingActions
      .pipe(untilDestroyed(this))
      .subscribe((event: MatOptionSelectionChange | null) => {
        this.handleAutoCompleteSelection(event);
      });
  }

  _filterOptions(value: string | AddressUnit): Observable<AddressUnit[]> {
    if (typeof value === 'string' && this.postalCode) {
      return AddressUtil.getFilteredStreets(value, this.postalCode, this.addressFormFieldFacade);
    }
    return of([]);
  }

  setDisabledState(isDisabled: boolean) {
    this._setDisabledState(isDisabled);
  }

  ngOnChanges(changes: SimpleChanges): void {
    if (changes['postalCode']) {
      const currentValue: AddressUnit | undefined = changes['postalCode'].currentValue;
      const previousValue: AddressUnit | undefined = changes['postalCode'].previousValue;

      this.handleMunicipalityAddressUnitChange(currentValue, previousValue);
    }
  }

  private handleMunicipalityAddressUnitChange(
    currentValue: AddressUnit | undefined,
    previousValue: AddressUnit | undefined
  ): void {
    if (currentValue !== previousValue) {
      this.filteredOptions = of([]);
      this.setupFilter();
    }
  }

  private handleAutoCompleteSelection(event: MatOptionSelectionChange | null): void {
    if (event && event.source.selected) {
      if (event.isUserInput || (this.autoCompleteTrigger && this.autoCompleteTrigger.activeOption)) {
        this.handleUserInputSelection(event.source.value, 'nameDU');
      } else {
        this.handleNoSelection();
      }
    }
  }

  private handleUserInputSelection(value: AddressUnit, subProperty: string): void {
    if (value && this.onChange) {
      this.control.setValue(value);
      const subPropertyValue = value[subProperty] ? value[subProperty] : '';
      this.onChange({
        value: subPropertyValue,
        addressUnit: value,
      });
    }
  }

  private handleNoSelection(): void {
    if (this.onChange) {
      this.onChange({
        value: this.control.value,
      });
    }
  }
}
